.intro {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    text-align: center;
}

.intro__inner-name {
    font-family: 'Sansita Swashed', cursive;
    margin-bottom: 20px;
    font-size: 50px;
}

.intro__inner-prof {
    font-size: 25px;
}

@media (max-width: 470px) {
    .intro__inner-name {
        font-size: 40px;
    }

    .intro__inner-prof {
        font-size: 25px;
    }
}

@media (max-width: 380px) {
    .intro__inner-name {
        font-size: 28px;
    }

    .intro__inner-prof {
        font-size: 20px;
    }
}