.aboutme {
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.aboutme__title {
    font-size: 35px;
    margin-bottom: 20px;
    width: 100%;
}

.aboutme__text {
    display: flex;
    flex-direction: column;
    font-size: 21px;
}

.aboutme__text-paragraph {
    margin-bottom: 10px;
}

@media (max-width: 500px) {
    .aboutme__text-paragraph {
        font-size: 17px;
    }
}

@media (max-width: 350px) {
    .aboutme__text-paragraph {
        font-size: 15px;
    }
}