.portfolio__item {
    padding-bottom: 30px;
    margin-bottom: 100px;
    border-bottom: 3px solid #fff;
    font-size: 30px;
}

.portfolio__title {
    margin-bottom: 5px;
    font-size: 30px;
}

.portfolio__subtitle {
    font-size: 23px;
    margin-bottom: 20px;
}

.portfolio__description {
    margin-bottom: 20px;
    font-size: 20px;
}

.portfolio__images {
    width: 100%;
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: center;
    flex-wrap: wrap;
}

.portfolio__image {
    border-radius: 10px;
    width: 50%;
    height: 100%;
    padding: 5px;
    margin: auto 0;
}

@media (max-width: 700px) {
    .portfolio__image {
        width: 100%;
    }
}

@media (max-width: 650px) {
    .portfolio__title {
        font-size: 25px;
    }
    
    .portfolio__subtitle {
        font-size: 21px;
    }
    
    .portfolio__description {
        font-size: 16px;
    }
}

@media (max-width: 400px) {
    .portfolio__title {
        font-size: 22px;
    }

    .portfolio__subtitle {
        font-size: 18px;
    }
}