.burger {
    transform: translateX(-90%);
    position: absolute;
    height: 100vh;
    padding: 30px 30px;
    transition: transform .8s ease;
}

.burger::after {
    content: '';
    width: 1px;
    top: 50px;
    bottom: 50px;
    right: 0;
    background-color: #fff;
    position: absolute;
}

.burger-field--active .burger {
    transform: translateX(0%);
}

.burger__list {
    display: flex;
    text-align: center;
    flex-direction: column;
}

.burger__link {
    margin: 10px 0;
    font-size: 20px;
    display: block;
    position: relative;
}

.burger-field {
    width: 0;
    height: 100vh;
    top: 0;
    position: fixed;
    transition: background-color .3s ease;
}

.burger-field--active {
    width: 100%;
    background-color: rgba(0, 0, 0, 0.8);
}

/* .burger__link .active {
    opacity: 0.8;
} */

.burger-btn {
    width: 40px;
    height: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    position: absolute;
    right: 0;
    top: 50px;
    transform: translateX(150%);
}

.burger-btn * {
    background: #fff;
}

.burger-line1 {
    position: absolute;
    bottom: 1px;
    width: 100%;
    height: 2px;
    transition: all 0.8s ease;
}

.burger-line2 {
    width: 100%;
    height: 2px;
    transition: all 0.3s ease;
}

.burger-line3 {
    position: absolute;
    top: 1px;
    width: 100%;
    height: 2px;
    transition: all 0.8s ease;
}

.burger-field--active .burger-btn .burger-line2 {
    opacity: 0;
}

.burger-field--active .burger-btn .burger-line1 {
    bottom: 35%;
    transform: rotate(-45deg);
}

.burger-field--active .burger-btn .burger-line3 {
    top: 51%;
    transform: rotate(45deg) translateY(50%);
}

@media (max-width: 600px) {
    .burger {
        transform: translateX(-95%);
    }

    .burger--open {
        transform: translateX(0%);
    }
}